.loader {
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .3);
  position: absolute;
}

.loader__circle {
  width: 100px;
  height: 100px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__circle--first, .loader__circle--second {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 8px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: loaderOne 1.6s linear infinite;
  position: absolute;
}

.loader__circle--first {
  border-top-color: #ff6b08;
}

.loader__circle--second {
  border-bottom-color: #ff6b08;
}

@keyframes loaderOne {
  0% {
    border-width: 10px;
    transform: rotate(0deg);
  }

  50% {
    border-width: 1px;
    transform: rotate(180deg);
  }

  100% {
    border-width: 10px;
    transform: rotate(360deg);
  }
}

@keyframes loaderTwo {
  0% {
    border-width: 1px;
    transform: rotate(0deg);
  }

  50% {
    border-width: 10px;
    transform: rotate(180deg);
  }

  100% {
    border-width: 1px;
    transform: rotate(360deg);
  }
}

.showLoader {
  display: flex;
}

.hideLoader {
  display: none;
}

